.wall {
  /* border: 2px solid #ddd; */
  /* border-top-left-radius: 10px;
  border-bottom-right-radius: 10px; */
  width: 100%;
  max-width: 600px;
  margin: 20px auto;
  padding: 10px 0;
  animation: slide 0.6s linear;
}
.wall .tasks {
  list-style-type: none;
  background-color: #bbb;
}

.wall .tasks li {
  margin: 5px 0;
  font-size: 22px;
  line-height: 32px;
  background-color: #ddd;
}

.wall .tasks li button {
  float: right;
  cursor: pointer;
  font-size: 22px;
  /* line-height: 24px; */
  border-radius: 5px;
}

.done {
  color: #808080;
  font-style: italic;
}

.wall .tasks li button:nth-child(1) {
  background-color: #f00;
  border: 2px solid #f00;
}

.wall .tasks li button:nth-child(2) {
  background-color: #00f;
  border: 2px solid #00f;
}

.wall .tasks li button:nth-child(3) {
  background-color: #0f0;
  border: 2px solid #0f0;
}

button svg {
  pointer-events: none;
}

@keyframes slide {
  0% {
    transform: translateY(15%);
  }
  100% {
    transform: translateY(0);
  }
}
